<template>
  <div class="login-input">
    <!-- 图片区域 -->
    <div class="avatar-box">
      <img src="~assets/img/login/logo.png" alt="logo.jpg">
    </div>
    <!-- 登录表单区域 -->
    <el-form ref="loginFormRef" 
      :model="loginForm" 
      :rules="loginFormRules" 
      class="login-form">
        
      <!-- 用户名区域 -->
      <el-form-item prop="username">
        <el-input 
        v-model="loginForm.username" 
        prefix-icon="el-icon-user" 
        placeholder="请输入用户名" 
        class="username">
        </el-input>
      </el-form-item>

      <!-- 密码区域 -->
      <el-form-item prop="password">
        <el-input 
        v-model="loginForm.password" 
        prefix-icon="el-icon-lock" 
        placeholder="请输入密码" 
        class="password" 
        type="password"
        show-password>
        </el-input>
      </el-form-item>

      <!-- 按钮区域 -->
      <el-form-item class="btns">
        <el-button type="login" @click="login">登录</el-button>
        <el-button type="register" @click="register" path="/register">注册</el-button>
        <el-button type="info" @click="reset">重置</el-button>
      </el-form-item>
          
    </el-form>
  </div>
</template>

<script>
  import { getAccountMsg } from 'network/login.js'
  export default {
  name: 'LoginInput',
  data() {
    return {
      // 表单的数据绑定对象
      loginForm: {
        username: '李四',
        password: '123456'
      },
      // 表单的验证规则
      loginFormRules: {
        // 验证用户名是否合法
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' }
        ],
        // 验证密码是否合法
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ]
      },
      usernameList: []
    }
  },
  methods: {
    login() {
      let isCheck = false
      for(let i of this.usernameList) {
        // 若用户名存在, 判断密码是否正确. 正确: 成功登录后跳转页面, 并且保存信息 失败: 提示用户密码错误
        // 若用户名不存在, 提示用户名错误或者不存在
        if(this.loginForm.username === i.username) {
          if(this.loginForm.password === i.password) {
            isCheck = true
            this.$router.push({ path: '/main' })
          }else {
            isCheck = true
            this.$message({showClose: true, message: '密码错误,请输入正确密码', type: 'error'})
          }
          let currentUserMsg = i
          this.$store.commit('addCurrentUserMsg', currentUserMsg)
        }
      }
      if(!isCheck) this.$message({showClose: true, message: '用户名不存在,请重新输入用户名', type: 'error'})
    },
    register() {
      if(this.$route.path != '/register') this.$router.push('/register')
    },
    reset() {
      this.$refs.loginFormRef.resetFields();
    },
  },
  created() {
    getAccountMsg().then(res => {
      this.usernameList = res
    })
  }
}
</script>

<style scoped>
  .login-input {
    width: 450px;
    height: 300px;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  .avatar-box {
    width: 130px;
    height: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(255, 255, 255, 0.5);
  }

  .avatar-box img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.5);
  }

  .login-form {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
  }
  
  .btns {
    display: flex;
    justify-content: flex-end;
  }
</style>