import axios from 'axios'

export function request(config) {
  //创建axios实例
  const instance = axios.create({
    baseURL: 'http://localhost:3000',
    timeout: 5000
  })

  //axios的拦截器
  instance.interceptors.request.use(config => {
    return config
  }, err => {
    return err
  })

  //响应拦截
  instance.interceptors.response.use(res => {
    // 返回请求正确的结果
    return res.data
  }, err => {
    // 错误的请求结果处理，这里的代码根据后台的状态码来决定错误的输出信息
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          err.message = '错误请求'
          break
        case 401:
          err.message = '未授权，请重新登录'
          break
        case 403:
          err.message = '拒绝访问'
          break
        case 404:
          err.message = '请求错误,未找到该资源'
          break
        case 405:
          err.message = '请求方法未允许'
          break
        case 408:
          err.message = '请求超时'
          break
        case 500:
          err.message = '服务器端出错'
          break
        case 501:
          err.message = '网络未实现'
          break
        case 502:
          err.message = '网络错误'
          break
        case 503:
          err.message = '服务不可用'
          break
        case 504:
          err.message = '网络超时'
          break
        case 505:
          err.message = 'http版本不支持该请求'
          break
        default:
          err.message = `连接错误${err.response.status}`
      }
    }else {
      err.message = "连接到服务器失败, 请重启服务器"
    }
  return Promise.reject(err.message)
  })
  //发送真正的网络请求
  return instance(config)
}